.worksContainer {
  min-width: 320px;
  margin: 0;
  padding: 0;
}

.worksWrapper {
  max-width: 1120px;
  min-width: 320px;
  min-height: 100vh;
  margin: 0 auto;
}

.worksList {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

@media (max-width: 320px) {
  .worksWrapper {
    max-width: 320px;
  }
}

@media (min-width: 768px) {
  .worksWrapper {
    min-width: 768px;
  }

  .worksList {
    flex-direction: column;
    justify-content: space-between;
  }
}

@media (min-width: 1120px) {
  .worksWrapper {
    min-width: 1120px;
  }

  .worksList {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}
